a.button,
button.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-self: start;
  background-color: #3e68ff;
  color: #fff;
  border-radius: 8px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
  padding: 0.25em 0.75em;
  min-width: 10ch;
  min-height: 44px;
  text-align: center;
  line-height: 1.1;
  transition: 220ms all ease-in-out;
}
a.button:hover, a.button:active,
button.button:hover,
button.button:active {
  background-color: #0037fe;
}
a.button:focus,
button.button:focus {
  outline-style: solid;
  outline-color: transparent;
  box-shadow: 0 0 0 4px #0029be;
}
a.button--small,
button.button--small {
  font-size: 1.15rem;
}
a.button--block,
button.button--block {
  width: 100%;
}

